    margin: auto;
    margin-left: 1em;
  }

  .link-list-item {
    margin: 10px;
    word-wrap:break-word;
  }

  .contact-links {
    outline: none;
    text-decoration: none;
    position: relative;
    font-size: 1em;
    color: #9e9ba4;
    display: inline-block;
    text-transform: uppercase;
    overflow: hidden;
    line-height: 1;
    top: 2px ;

  }
  .contact-links:hover {
    color:rgb(61, 54, 54);
    text-decoration: underline;
    text-transform: uppercase;
    overflow: hidden;
    line-height: 1;
    color: #b2b0a9;
  }
  .contact-links::after{
    content: '';
    position: absolute;
    height: 16px;
    width: 100%;
    top: 50%;
    margin-top: -8px;
    right: 0;
    background: #f9f9f9;
    transform: translate3d(-100%,0,0);
    transition: transform 0.4s;
    transition-timing-function: cubic-bezier(0.7,0,0.3,1);
  }

  .contact-links:hover::after {
    transform: translate3d(100%,0,0);
  }

  .contact-links::before{
      content: attr(data-letters);
      position: absolute;
      z-index: 2;
      overflow: hidden;
      color: #424242;
      white-space: nowrap;
      width: 0%;
      transition: width 0.4s 0.3s;
  }
    .contact-links:hover::before {
      width: 100%;
  }

