.footer-container {
    background-color: #0d1b2a;
    padding: 4rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.footer-header-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #e0e1dd;
}

.footer-heading {
    margin-bottom: 1.5rem;
    font-size: 24px;
}

.footer-links {
    width: 100% ;
    max-width: 1000px;
    display: flex;
    justify-content: center;

}

.footer-link-wrapper {
    display: flex;
}

.footer-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 4px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;
}

.footer-link-items h2 {
    margin-bottom: 16px;
}

.footer-link-items > h2 {
    color: #e0e1dd;
}

.footer-link-items a {
    color: #e0e1dd ;
    text-decoration: none;
    margin-bottom: 8px;
    padding-top: 12px;
    padding-bottom: 0px;
    padding-left: 20px;
    padding-right: 20px;
}

.footer-link-items a:hover {
    color: #fff;

    background-color: #242424;
    border: "10px solid #242424";
    transition: 0.3s ease-out;
}

.footer-email-form h2 {
    margin-bottom: 32px;
}

.footer-input::placeholder {
    color: #b1b1b1;
}

.social-icon-link {
    color: #e0e1dd;
    font-size: 24px;
}

.social-media {
    max-width: 1000px;
    width: 100%;
    text-align: center;
}

.social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1000px;
    margin: auto;
}

.social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
}

.social-logo {
    color: #e0e1dd;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    align-self: center;
    margin-bottom: 16px;
}
.footer-logo {
    color: #e0e1dd;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    width: 100%;
    font-family: 'Times New Roman', Times, serif;

  }
  .footer-logo-img {
    width: 1.5em;
  }
.website-rights {
    color: #e0e1dd;
    margin-bottom: 16px;
    padding-top: 20px;
}

.footer-logo {
    width: 90%;
}
.credit-invis{
    color: #242424;
}
.credit-invis:hover{
    color: #2c2c2c;
}


@media screen and (max-width: 820px) {
  
    .footer-input {
      width: 100%;
    }
  
    .btn {
      width: 100%;
    }
  
    .footer-link-wrapper {
      flex-direction: column;
    }
  
    .social-media-wrap {
      flex-direction: column;
      justify-content: center;
      margin-left: 0;
    }
    .website-rights {
        padding-top: 1em;
    }


  }
  
