.navbar {
    background: linear-gradient(90deg, #0d1b2a  0%, rgb(27, 38, 59) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    width: 100%;
    font-family: 'Times New Roman', Times, serif;

  }
  .navbar-logo-wrapper {
    display: flex;
    flex-shrink: 2;
  }

  .navbar-logo-img {
    width: 1.5em;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 5px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 1.5rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #e0e1dd;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .nav-links:hover {
    color: #fff;
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #e0e1dd;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }

  .strat-icon{
    margin-left: 10px;
    max-width: 50px;
    height: 50px;
  }
  
  @media screen and (max-width: 901px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }


    .navbar-logo-wrapper {
      display: flex;
    }
  
    .nav-menu.active {
      background: #0d1b2a;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
      justify-content: start;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
      word-wrap: normal;
    }
  
    .nav-links:hover {
      background-color: #e0e1dd;
      color: #0d1b2a;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: -.63em;
      left: -.55em;
      transform: translate(25%, 50%);
      width: auto;
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #e0e1dd;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #e0e1dd;
      padding: 14px 20px;
      border: 1px solid #e0e1dd;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }

    .strat-icon{
      margin-left: 10px;
      max-width: 50px;
      height: 50px;
    }
  }